  margin-top: -30px;
  margin-left: 40px;
  color: #262223;
  font-size: 1rem;
  line-height: 1em;
}

@-webkit-keyframes mouse-scroll {
  to {
    transform: translate(0, 6px) scale(1, 0.8);
  }
}

@keyframes mouse-scroll {
  to {
    transform: translate(0, 6px) scale(1, 0.8);
