/*
  ItemUI CSS
*/
.itemui__card {
  display: grid;
  grid-template-columns: 300px;
  grid-template-rows: 210px 210px 80px;
  grid-template-areas: "image" "text" "stats";
  border-radius: 18px;
  background: #f2f2f2;
  text-align: center;
  margin-top: 20px;
  color: #262223;
  transition: 0.5s ease;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.6);
}

.itemui__image {
  grid-area: image;
  background-size: cover;
  background-position: center;
}

.itemui__text {
  grid-area: text;
  color: #262223;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.itemuitext__projectname {
  margin-top: 0;
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: capitalize;
}

.itemui__stats {
  grid-area: stats;
  background-color: #262223;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.itemui__stats a {
  color: #f2f2f2;
  padding: 10px;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: capitalize;
}

