  justify-content: center;
  align-items: center;
  padding: 10px 0;
}

.navbar__menuitem {
  position: relative;
  color: #f2f2f2;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0 10px;
  outline: none;
  font-weight: 600;
  transition: 0.6s;
  letter-spacing: 2px;
  margin: 0 15px;
  cursor: pointer;
}

.navbar__menuitem--active {
  color: #f2811d;
  border-bottom: 2px solid #f2811d;
}

.navbar__showmenubtn {
  padding: 0 10px;
}

.navbar__showmenubtn,
.navbar__closemenubtn {
  transition: 0.6s;
  color: #f2f2f2;
  font-size: 2em;
  cursor: pointer;
  display: none;
}

.navbar__showbtn {
  position: relative;
  top: 5px;
}

.navbar__menuitem:hover,
.navbar__showmenubtn:hover,
.navbar__closemenubtn:hover {
  color: #f2811d;
}

#navbar-chk {
  position: absolute;
  visibility: hidden;
  z-index: -1111;
}

.navbar__container--sticky {
  padding: 5px 100px;
  background: #262223;
  opacity: 1;
  z-index: 100;
}

@media screen and (max-width: 812px) {
  .navbar__container {
    padding: 15px 10px;
    z-index: 100;
  }

  .navbar__showmenubtn,
  .navbar__closemenubtn {
    display: block;
    transition: 0.6s;
  }

  .navbar__menu {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #262223;
    right: -100%;
    top: 0;
    text-align: center;
    padding: 100px 0;
    line-height: normal;
    transition: 0.6s;
    opacity: 0.986;
    flex-flow: column nowrap;
  }

  .navbar__menuitem {
    display: block;
    color: #f2f2f2;
    padding: 20px;
    font-size: 2rem;
    transition: 0.6s;
  }

  .navbar__container--sticky {
    padding: 5px 10px;
  }

  .navbar__menuitem--active {
    color: #f2811d;
    border: none;
  }

  .navbar__closemenubtn {
    position: absolute;
    top: 30px;
    right: 30px;
  }

  .navbar__menu-open {
    right: 0;
  }

  .navbar__showmenubtn-display {
    opacity: 0;
  }
}

